@import '~antd/dist/antd.css';

.App{
  font-family: Arial, sans-serif;;
  height: 100vh;
  background-image: url("./assets/images/bg.jpg");
  background-position: center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  user-select: none;
  transform: translate(-50%, -50%);

  h1,h2,h3,h4,h5,p,span,strong{
    color: white;
    margin-bottom: 0;
    user-select: none;
  }

  img{
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;
  }

  .button{
    cursor: pointer;
    user-select: none;
    color: white;
    // width: 50%;
    width: fit-content;
    // margin: 1% auto 1% auto;
    background: #F3903F;
    // box-shadow: 0 3px 6px #000000;
    padding: 5px 15px;
    text-align: center;
    border-radius: 50px !important;
    font-size: 1vw;
    font-weight: 700;
    border: 2px solid white
  }


  .button:hover{
    opacity: 0.9;
  }

  .button:active{
    opacity: 1;
  }

  @media(max-width: 800px){
    background-image: url("./assets/images/mobileBg.jpg");

    .button{
      font-size: 1vh;
    }
  }
}

.floatRight{
  float: right;
}

.ant-modal-content{
  border-radius: 40px;
  font-family: Arial, sans-serif;
  // background-image: url("./assets/images/box.png");
  background-position: center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-color: #212333;
  padding: 30px 0 40px 0;
  box-shadow: none;
  color: #F7A42D !important;
  .ant-modal-close{
    top: 15px;
    right: 20px;

    .ant-modal-close-x{
      color: #F7A42D;
      font-size: 23px;
    }
  }
  .miningRuleModalWrap, .referralRuleModalWrap{
    h2{
      color: #F7A42D;
      font-size: 20px;
      font-weight: 600;
    }
    p{
      color: #F7A42D;
      font-size: 14px;
    }
  }
}

.inputBuy{
  width:30%;
  font-size: 16px;
  background: transparent;
  border-radius: 7px;
  margin: 0 15px;
  text-align: center;
}

.buttonGroups{
  .button{
    width: 30%;
    margin: 10px;
    vertical-align: middle;
    padding: 5px 0;
    border: 1px solid #F7A42D;
    border-radius: 10px;
    font-size: 16px;
    text-align: center;
    cursor: pointer;
    display: inline-block;
  }
}

.ant-popover{
  width: 400px;
  max-width: 90%;
}

@media(max-width:765px){
  .ant-modal-content{

    .miningRuleModalWrap{
      p{
        color: #F7A42D;
        font-size: 14px;
        line-height: 16px;
      }
    }
  }
}
