.staking{
  position: relative;

  .totalMinerWrap{
    width: 17%;
    position: absolute;
    top: 5%;
    left: 3%;
    img{
      width: 100%;
    }

    .contentWrap{
      width: 90%;
      position: absolute;
      top: 8%;
      left: 5%;

      h2{
        font-size: 1.3vw;
        width: 30%;
        color: black;
        font-weight: bold;
      }

      p{
        position: absolute;
        left: 60%;
        bottom: 10%;
        transform: translateX(-50%);
        color: black;
        font-weight: bold;
        font-size: 1.2vw;
      }
    }
  }

  .textLogo{
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    width: 33%;
    z-index: 2;
  }

  .walletBalanceWrap{
    position: absolute;
    top: 5%;
    right: 3%;
    width: 17%;
    z-index: 2;
    img{
      width: 100%;
    }
    .contentWrap{
      width: 70%;
      position: absolute;
      top: 59%;
      transform: translateY(-50%);
      left: 20%;

      h2{
        font-size: 1vw;
        color: black;
        font-weight: bold;
      }

      p{
        color: black;
        text-align: center;
        font-weight: bold;
        font-size: 1.2vw;
      }
    }
  }

  .fundWrap{
    position: absolute;
    top: 8%;
    right: 1%;
    width: 20%;
    z-index: 1;
    img{
      width: 100%;
    }
    .contentWrap{
      width: 92%;
      position: absolute;
      top: 68%;
      transform: translateY(-50%);

      h1{
        font-size: 1.5vw;
        color: black;
        margin-bottom: 1%;
      }
      h2{
        font-size: 1.3vw;
        color: black;
        font-weight: 600;
        margin-bottom: 1%;
      }

      p{
        color: black;
        text-align: center;
        font-weight: normal;
        font-size: 1vw;
      }
    }
  }

  .socialMediaWrap{
    position: absolute;
    bottom: 5%;
    left: 5%;
    width: 40%;

    img, div{
      display: inline-block;
      vertical-align: middle;
      color: white;
      margin: 0 15px;
    }
    img{
      width: 40px;
      border-radius: 20px;
    }

    div{
      .selected{
        color: #edb58c;
        font-weight: bold;
      }
    }
  }

  .dividendsWrap{
    position: absolute;
    bottom: 20%;
    left: 15%;
    width: 32%;
    img{
      width: 100%;
    }
    .dividendsWrapMobile{
      display: none;
    }
    .contentWrap{
      width: 80%;
      position: absolute;
      top: 10%;
      left: 10%;
      h1{
        font-size: 2.5vw;
        font-weight: 600;
        margin: 2% 0;
      }

      h2{
        font-size: 1.5vw;
        font-weight: 400;
        margin-bottom: 10px;
      }

      h3{
        font-size: 1.7vw;
        font-weight: 400;
      }

      .dataWrap{
        padding: 0 8%;
        margin-top: 7.5%;
        p{
          width: 100%;
          font-size: 1.1vw;

          span,strong{
            color:#4d2f0f;
            font-weight: 600;
          }
        }
      }


    }
  }

  .stakeUnstakeWrap{
    position: absolute;
    bottom: 22%;
    right: 24%;
    width: 23%;
    img{
      width: 100%;
    }
    .contentWrap{
      position: absolute;
      top: 6%;
      left: 18%;
      width: 80%;
      .topWrap, .bottomWrap{
        width: 70%;
        h1{
          font-size: 1.5vw;
          color: black;
          margin-bottom: 2.5%;
        }
        h2{
          font-size: 1.2vw;
          color: black;
        }

        p{
          text-align: center;
          font-size: 1vw;
          color: black;
        }

        .ant-input-affix-wrapper{
          height: 2vw;
          margin: 0 0 3% 0;
          position: relative;
          border: 1px solid black;
          box-sizing: border-box;
          border-radius: 5px;
          background-color: transparent;

          .ant-input-suffix, .ant-input-prefix{
            color: black;
            font-size: 14px;
            font-weight: bold;
            cursor: pointer;
            user-select: none;
            span{
              color: black;
            }
          }
          .suffix{
            padding-right: 0px;
          }

          .ant-input{
            border:none;
            padding-left: 0;
            padding-right: 5px;
            color: #4d2f0f;
            font-size: 14px;
            font-weight: bold;
            line-height: 2vw;
            text-align: left;
            background-color: transparent;
          }

          .ant-input:hover {
            border: none !important;
          }

          .ant-input:focus {
            border: none !important;
            outline: 0;
            box-shadow: none !important;
          }
        }

        .ant-input-affix-wrapper-focused{
          outline: 0;
          border-right-width: 1px !important;
          box-shadow: none !important;
        }
      }

      .topWrap{

      }

      .bottomWrap{
        margin-left: 10%;
        padding-top: 13%;
      }
    }
  }

  .claimWrap{
    position: absolute;
    bottom: 22%;
    right: 1%;
    width: 20%;
    img{
      width: 100%;
    }
    .contentWrap{
      position: absolute;
      top: 4%;
      left: 5%;
      width: 90%;

      .button{
        width: 30%;
      }
      p{
        color: black;
        font-size: 1vw;
        line-height: 1.1vw;
        span, strong{
          color: #4d2f0f;
          font-weight: 600;
        }
      }

      .unstaked{
        margin-top: 7%;
      }

      .divClaim{
        margin-top: 10%;
        position: relative;
        .button{
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
  }
  .miningRulesWrap{
    right: initial !important;
    left: 3%;
  }
  .referralRulesWrap{
    z-index: 2;
    position: absolute;
    bottom: 22%;
    right: 3%;
    width: 7% !important;
    background: none !important;
    box-shadow: none !important;
    margin: 0 !important;
    img{
      width: 100%;
    }
    .contentWrap{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      width: 50%;
      font-size: 1.3vw;
      color: #4D2F0E;
    }
  }

  .pancakeCountdownWrap{
    position: absolute;
    bottom: 4%;
    left: 50%;
    transform: translateX(-50%);
    text-align:center;

    h2{
      font-size: 1vw;
    }

    .buttonGroups{
      margin-top: 10px;
      a{
        color: black;
        background: #FFD3B3;
        height: 40px;
        display: inline-block;
        line-height: 40px;
        border-radius: 10px;
        padding:  0 20px;
        margin: 0 10px;
        font-size: 16px;
        img{
          height: 30px;
          margin-right: 10px;
        }
      }
    }
  }

  @media(max-width: 900px){
    .totalMinerWrap{
      width: 40%;
      top: 2%;

      .contentWrap{
        h2{
          font-size: 1.4vh;
        }

        p{
          bottom: 0;
          font-size: 1.6vh;
        }
      }
    }

    .textLogo{
      top: 10%;
      width: 80%;
    }

    .walletBalanceWrap{
      width: 40%;
      top: 2%;

      .contentWrap{

        h2{
          font-size: 1.3vh;
        }

        p{
          font-size: 1.5vh;
        }
      }
    }

    .socialMediaWrap{
      bottom: 3%;
      left: 0;
      width: 100%;
      img{
        width: 35px;
        margin: 0 10px;
      }

      div{
      }
    }

    .fundWrap{
      top: 9%;
      right: initial;
      left: 50%;
      transform: translateX(-50%);
      width: 70%;

      .contentWrap{
        top: 70%;
        h1{
          font-size: 2vh;
          margin-bottom: 2%;
        }
        h2{
          font-size: 1.9vh;
          margin-bottom: 3%;
        }

        p{
          font-size: 1.6vh;
        }
      }
    }

    .dividendsWrap{
      bottom: 43%;
      left: 50%;
      transform: translateX(-50%);
      width: 85%;
      img{
        display: none;
      }
      .dividendsWrapMobile{
        display: block;
      }
      .contentWrap{
        h1{
          font-size: 3vh;
          margin: 3% 0;
        }

        h3{
          font-size: 2vh;
        }

        .dataWrap{
          padding: 0 3%;
          margin-top: 10%;
          p{
            font-size: 1.5vh;
          }
        }
      }
    }

    .stakeUnstakeWrap{
      position: absolute;
      bottom: 12%;
      right: 0;
      width: 50%;
      img{
        width: 100%;
      }
      .contentWrap{

        .topWrap, .bottomWrap{
          h1{
            font-size: 1.5vh;
          }
          h2{
            font-size: 1.2vh;
          }

          p{
            font-size: 1vh;
          }

          .ant-input-affix-wrapper{
            height: 2vh;
            margin: 0 0 0 0;
            padding: 0 10px;
            .ant-input-suffix, .ant-input-prefix{
              font-size: 1vh;
              font-weight: normal;
            }
            .suffix{
              padding-right: 0px;
            }

            .ant-input{
              padding-right: 0px;
              font-size: 1vh;
              line-height: 2vh;
            }
          }
        }

        .bottomWrap{
          padding-top: 11%;
        }
      }
    }

    .claimWrap{
      bottom: 13%;
      right: initial;
      left: 1.5%;
      width: 47%;
      .contentWrap{

        p{
          color: black;
          font-size: 1.2vh;
          line-height: 1.3vh;
        }

        .unstaked{
          margin-top: 7.5%;
        }

        .divClaim{
          margin-top: 9%;
          .button{
            top: 35%;
          }
        }
      }
    }

    .miningRulesWrap{
      left: 1% !important;
    }
    .referralRulesWrap{
      bottom: 12% !important;
      right: 2%;
      width: 17% !important;

      .contentWrap{
        font-size: 1.5vh;
      }
    }
    .pancakeCountdownWrap{
      display: none;
    }
  }
}
