.home {
  position: relative;

  .totalMinerWrap {
    // width: 17%;
    position: absolute;
    top: 5%;
    left: 3%;
    img {
      width: 100%;
    }

    .contentWrap {
      background-color: #212333 !important;
      padding: 15px;
      width: 100%;
      display: flex;
      flex-direction: column;
      border-radius: 20px;
      h2 {
        font-size: 18px;
        font-weight: bold;
        color: #f7a42d !important;
      }

      p {
        font-weight: bold;
        font-size: 18px;
        color: #f7a42d !important;
      }
    }
  }

  .textLogo {
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    width: 8%;
  }

  .walletBalanceWrap {
    position: absolute;
    top: 5%;
    right: 3%;

    img {
      width: 100%;
    }
    .contentWrap {
      width: 100%;
      display: flex;
      align-items: center;
      gap: 15px;
      background-color: #ffffff;
      padding: 10px;
      border-radius: 100px;
      h2 {
        font-size: 16px;
        color: black;
        font-weight: bold;
      }

      p {
        color: black;
        text-align: center;
        font-weight: bold;
        font-size: 16px;
      }
    }
  }

  .halfWrap {
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 450px;
    display: flex;
    justify-content: center;
    gap: 25px;
    .infoItem {
      flex: 1;
      width: 100%;
      background-color: #212333;
      border-radius: 20px;
      padding: 15px;
      display: flex;
      flex-direction: column;
      align-items: center;
      h2 {
        font-size: 16px;
        color: #f7a42d;
        text-align: center;
        &:nth-child(2) {
          font-weight: 700;
        }
      }
    }
  }

  .socialMediaWrap {
    position: absolute;
    bottom: 13%;
    left: 5%;
    // width: 40%;

    img,
    div {
      display: inline-block;
      vertical-align: middle;
      color: white;
      margin: 0 15px;
    }
    img {
      width: 40px;
      border-radius: 20px;
    }

    div {
      .selected {
        color: #edb58c;
        font-weight: bold;
      }
    }
  }

  .miner {
    position: absolute;
    bottom: 20%;
    left: 13%;
    width: 13%;
  }

  .mineWrap {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);

    img {
      width: 100%;
    }
    .mobileMineWrap {
      display: none;
    }
    .contentWrap {
      width: 100%;
      padding: 15px;
      width: 100%;
      max-width: 450px;
      background-color: #212333;
      border-radius: 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      color: #f7a42d;

      h3 {
        text-align: left;
        font-size: 1.3vw;
        color: #f7a42d;
      }

      h1 {
        font-size: 2.1vw;
        font-weight: 600;
        color: #f7a42d;
      }

      h2 {
        font-size: 1.3vw;
        font-weight: 600;
        color: #f7a42d;
      }

      .countdownWrap {
      }

      .buttonGroup {
        .button {
          display: inline-block;
        }

        .buttonDisabled {
          background-color: grey;
        }
      }

      .button {
        margin: 10px;
        width: 100%;
      }

      .boostCountdownWrap {
        margin-bottom: 5px;
        color: #f7a42d !important;
        font-size: 1.1vw;
      }

      p {
        width: 100%;
        font-size: 1vw;
        color: #f7a42d;
      }
      span {
        color: #f7a42d;
      }
    }
  }

  .referralWarp {
    position: absolute;
    bottom: 14%;
    right: 5%;
    background-color: #212333;
    padding: 10px;
    img {
      width: 100%;
    }
    .contentWrap {
      h2 {
        font-size: 1.2vw;
        color: #f7a42d;
      }

      p {
        margin-top: 25px;
        text-align: center;
        font-size: 1.3vw;
        color: white;
      }

      .button {
      }
    }
  }

  .dividendsWrap {
    right: initial !important;
    left: 3%;
  }
  .ruleWrap {
    position: absolute;
    bottom: 14%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 25px;
    .rules {
      color: white;
      font-weight: bold;
    }
  }

  .pancakeCountdownWrap {
    position: absolute;
    bottom: 4%;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;

    h2 {
      font-size: 1vw;
    }

    .buttonGroups {
      margin-top: 10px;
      a {
        color: black;
        background: #ffd3b3;
        height: 40px;
        display: inline-block;
        line-height: 40px;
        border-radius: 10px;
        padding: 0 20px;
        margin: 0 10px;
        font-size: 16px;
        img {
          height: 30px;
          margin-right: 10px;
        }
      }
    }
  }

  @media (max-width: 900px) {
    .totalMinerWrap {
      top: 15%;
      width: 95%;
      .contentWrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        h2 {
          font-size: 14px;
        }
        p {
          font-size: 14px;
        }
      }
    }

    .textLogo {
      top: 2%;
      left: 15%;
      width: 30%;
    }

    .walletBalanceWrap {
      width: 40%;
      top: 2%;

      .contentWrap {
        h2 {
          font-size: 1.3vh;
        }

        p {
          font-size: 1.5vh;
        }
      }
    }

    .halfWrap {
      position: absolute;
      top: 25%;
      left: 50%;
      transform: translateX(-50%);
      width: 95%;
      h2 {
        font-size: 14px !important;
      }
    }

    .socialMediaWrap {
      //display: none;
      bottom: 1%;
      left: 0;
      width: 100%;
      img {
        width: 35px;
        margin: 0 10px;
      }

      div {
      }
    }

    .miner {
      bottom: 12%;
      left: 17%;
      width: 15%;
    }

    .mineWrap {
      bottom: initial;
      top: 40%;
      width: 95%;

      img {
        display: none;
      }
      .mobileMineWrap {
        display: block;
      }

      .contentWrap {
        width: 100%;

        h3 {
          font-size: 1.8vh;
        }

        h1 {
          font-size: 2.5vh;
        }

        h2 {
          font-size: 1.7vh;
        }

        .button {
          font-size: 1.5vh;
        }

        .boostCountdownWrap {
          font-size: 1.5vh;
        }
        p {
          font-size: 1.3vh;
        }
      }
    }

    .referralWarp {
      position: absolute;
      bottom: 20%;
      width: 100%;
      right: 50%;
      transform: translateX(50%);
      img {
        width: 100%;
      }
      .contentWrap {
        width: 100%;
        h2 {
          text-align: center;
          font-size: 1.6vh;
        }

        p {
          text-align: center;
          font-size: 1.5vh;
        }

        .button {
          margin: 5px auto -10% auto;

          font-size: 1.3vh;
        }
      }
    }
    .ruleWrap {
      bottom: 10%;
      width: 95%;
      justify-content: space-around;
    }

    .miningRulesWrap {
      left: 33% !important;
    }

    .dividendsWrap {
      left: 1% !important;
    }

    .referralRulesWrap {
      bottom: 12% !important;
      right: 2%;
      width: 17% !important;

      .contentWrap {
        font-size: 1.5vh;
      }
    }

    .pancakeCountdownWrap {
      bottom: 39%;

      h2 {
        font-size: 1.4vh;
        color: black;
        span,
        strong {
          color: black;
        }
      }
      .addressInfo {
        span {
          padding-right: 5px;
          display: inline-block;
          width: auto;
          color: black;
          vertical-align: middle;
        }

        .address {
          width: 70%;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow-x: hidden;
        }
      }

      .buttonGroups {
        margin-top: 10px;
        a {
          height: 30px;
          line-height: 30px;
          font-size: 14px;
          img {
            height: 20px;
          }
        }
      }
    }
  }
}
