
.mineInfo{
  width: 400px;
  max-width: 100%;
  margin: 0 auto;
  h1{
    color: white;
  }
  p{
    margin-bottom: 10px;
  }
  .button{
    width: 30%;
    margin-left: auto;
    vertical-align: middle;
    padding: 5px 0;
    border: 1px solid #4D2F0E;
    border-radius: 10px;
    font-size: 16px;
    text-align: center;
    cursor: pointer;
  }

  .buttonGroups{
    position: relative;
    width: 100%;
    .button{
      text-align: center;
      width: 30%;
      display: inline-block;
      margin:  0 10%;
    }
  }
}
